@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;700;800;900&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: 'Poppins', sans-serif;
  background-image: linear-gradient(#769c69,#d3d353);
  height: 100vh;
  display: flex;
  align-items: center;
}

img{
  cursor: pointer;
  height: 180px;
  mix-blend-mode:color-burn;
  margin: 24px;
}

img:hover{
  opacity: 0.75;
}

.App{
  display: flex;
  width: 100vw;
  justify-content: space-between;
}

.SavedContacts{
  display: flex;
  flex-direction: column;
  align-items: center;
  color: aliceblue;
  width: 60%;
}

.NewContact{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #d3d353;
    padding: 18px 40px;
    border-radius: 22px;
    width: 40%;
}

h1{
  font-size: 36px;
  color: #262b24;
  place-items: center;
}

form{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.first-name,.last-name,.number{
  margin: 5px;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items:baseline;
  gap: 5px;
}

input{
  width: 100%;
  background-color: #769c69;
  color: aliceblue;
  padding: 10px;
  font-size: 12px;
  border-radius: 15px;
  border: 1px solid #769c69;
}

button{
  margin: 10px;
  cursor: pointer;
  padding: 10px;
  font-size: medium;
  border-radius: 15px;
  background-color: #36de18;
  border: 1px solid #36de18;
}

button:hover{
  background-color: #d3d353;
  color: aliceblue;
}

.searchBar{
  margin-top: 20px;
  padding: 12px;
  display: flex;
  gap: 40px;
  width: 80%;
}

.searchBox{
  font-size: 16px;
}

.searchBar button{
  margin: 0px;
  width: 30%;
}

.cardsContainer{
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.contactCard{
  display: flex;
  align-items: center;
  width: 90%;
  justify-content: space-between;
}

span{
  margin-left: 15px;
  color:#769c69;
}